      link: 'https://www.meetup.com/Munich-Datacademy/events/273247944/',
      description: '<p>Seien Sie live dabei und diskutieren Sie mit, wenn Branchenexperten, Fach- und Führungskräfte sich im Talkshow-Format über echte digitale Transformation und erfolgreiche Umsetzung austauschen.</p> <p>Schnelle Innovation ist die DNA einer erfolgreichen digitalen Transformation. Um mit dem heutigen, immer vernetzteren Kunden mithalten zu können, benötigen Unternehmen die richtige Strategie und Technologie. Ihre Kunden erwarten ein außergewöhnliches Kundenerlebnis - auf jedem Kanal und zu jeder Zeit. Diese Disziplin lässt sich nur mit nutzerzentrierten Innovationen in der Kundeninteraktion und der Prozessautomatisierung über verschiedene Funktionsbereiche hinweg beherrschen.</p> <p>Erhalten Sie wertvolle Impulse zu folgenden Aspekten:<br/>- Nutzen Sie Lösungen für eine schnelllebige Welt, um schnell neue Strategien umsetzen zu können<br/>- Setzen Sie auf proaktive Kundenbindung durch überzeugende, persönliche Angebote<br/>- Verkaufen Sie mit Fingerspitzengefühl, indem Sie Ihren Kunden auch in schwierigen Zeiten immer das anbieten, was sie brauchen</p> <p>Erleben Sie in diesem Online-Meet-up Case-Studies von Branchenführern aus erster Hand, spannende Diskussionen zu zukunftsfähigen Kundeninteraktionsstrategien und stellen den Referenten direkte Fragen.</p> <p>Gemeinsam mit unseren Kunden arbeiten wir an der digitalen Welt von morgen und an einer Lösung, wie wir unsere Mitarbeiter und Kunden in diesem Wandel bestmöglich unterstützen können. Dabei treiben wir die digitale Transformation mit führenden Organisationen, wie Siemens, Deutsche Telekom, Deutsche Bahn, DAK, Ferring, Roche, Erste Group... voran.</p> <p>Z. B. implementierte Siemens Pega als Standardplattform für Prozess- und digitales Case-Management mit dem Ziel einer globalen End-to-End-Digitalisierung. Ein paar Ergebnisse:<br/>- Effizienzverbesserung um 80-90%<br/>- Ein Zehntel der Kosten bei 10-facher Geschwindigkeit und unendlicher Skalierbarkeit<br/>-[masked] Benutzer weltweit</p> <p>Wir freuen uns auf Ihre Teilnahme!</p> <p>Anmeldung unter: <a href="http://bit.ly/pega2810" class="linkified">http://bit.ly/pega2810</a></p> ',
