      description: '<p>Unser Social Dinner goes Oktoberfest.</p> <p>Wer Lust daran hat kann an diesem Abend gerne in Tracht kommen. Auf der Speisekarte gibt es zusätzlich eine paar Oktoberfestschmankerl.</p> <p>Ich freue mich auf dich.</p> <p>WAS IST SOCIAL DINNER?<br/>Gutes Essen genießen und ein geselliges Miteinander erleben. Wer dabei sein möchte bitte anmelden.</p> <p>WAS DICH ERWARTET:<br/>Ich bin Gastgeberin aus Leidenschaft und glaube, dass wir zusammen mehr erreichen können. Zu diesem Zweck habe ich das togather Anfang des Jahres eröffnet.</p> <p>Wünscht du dir in einer Wohlfühlatmosphäre genussvoll und in Muße speisen zu können?</p> <p>Willst du dich in einem geselligen Rahmen mit Gleichgesinnten austauschen und dein soziales wie berufliches Netzwerk erweitern?</p> <p>Im togather lernst du neue interessante Menschen kennen, führst vielseitige Gespräche und gehst anschließend beschwingt und motiviert in deinen Tag zurück. Du gewinnst Lebensqualität durch liebevoll zubereitetes Essen in gemütlicher Atmosphäre.</p> <p>Klingt das gut? Dann bis Freitag!</p> ',
